<template>
  <div @click="jump">{{ test }}</div>
</template>

<script>
import { getUserAgent } from "../../util";
export default {
  data() {
    return {
      qrcode_id: "",
      test: "",
    };
  },
  mounted() {
    this.qrcode_id = this.$route.query.qrcode_id;
    // 检测是否为移动端
    if (this.$store.state.viewListen) {
      // 检测设备类型，判断是安卓还是ios直接拉起对应的应用
      console.log(this.$route.query);
      getUserAgent()
        .then((android) => {
          console.log(android);
          try {
            window.location.href =
              "qbaytek://www.qbaytek.com:80/app/customer/qrcode?qrcode_id=" +
              this.qrcode_id;
            setTimeout(() => {
              throw new Error("没有成功拉起androi!");
            }, 500);
          } catch (err) {
            this.test = this.qrcode_id;
            window.location.href = `https://order.qbaytek.com/#/about?qrcode_id=${this.qrcode_id}`;
            console.log(err);
          }
        })
        .catch((ios) => {
          console.log(ios);
          try {
            window.location.href = "Qbay://?qrcode_id=" + this.qrcode_id;
            setTimeout(() => {
              throw new Error("没有成功拉起androi!");
            }, 500);
          } catch (err) {
            console.log(err, "err");
            this.test = this.qrcode_id;
            window.location.href = `https://order.qbaytek.com/#/about?qrcode_id=${this.qrcode_id}`;
            console.log(err);
          }
        });
    }
  },
  methods: {
    jump() {
      window.location.href =
        "qbaytek://www.qbaytek.com:80/app/customer/qrcode?qrcode_id=" +
        this.qrcode_id;
    },
  },
};
</script>

<style scoped lang="scss">
div {
  width: 100%;
  height: 100vh;
}
</style>
